<template>
  <article class="pricing_container">
    <h3 class="wow bounceIn">{{ title }}</h3>
    <section class="pricing_list">
      <div
        class="wow fadeInUp priceCart"
        v-for="(version, index) in data"
        :key="index"
      >
        <h4>{{ version.name }}</h4>
        <div class="featuresContainer">
          <p v-for="(spec, index) in version.spacificationList" :key="index">
            <i class="far fa-check-circle"></i>
            {{ spec }}
          </p>
        </div>
        <p class="price">{{ version.cost + " " + currency }}</p>
        <button @click="buyVersion(version)">{{ version.btnText }}</button>
      </div>
      <ModalComponent v-model="isShow" :close="closeModal">
        <section class="modal_container">
          <SendClientDataToBuy
            class="submit_Container show-up"
            v-bind="propObj"
          />
        </section>
      </ModalComponent>
    </section>
  </article>
</template>

<script>
import SendClientDataToBuy from "@/components/SendClientDataToBuy";
export default {
  data() {
    return {
      isShow: false,
      propObj: {},
    };
  },
  components: {
    SendClientDataToBuy,
  },
  name: "ProjectPagePricing",
  props: ["projectName", "title", "data", "designYourNeeds"],
  methods: {
    closeModal() {
      this.isShow = false;
    },
    buyVersion(version) {
      this.propObj = {
        currency: this.currency,
        designYourNeeds: this.designYourNeeds,
        submitAction: "submitAction",
        totalPrice: version.cost,
        closeModal: this.closeModal,
        versionName: version.name,
        programName: this.projectName,
      };
      this.isShow = true;
    },
  },
  computed: {
    currency() {
      const { lang } = this.$store.state;
      return this.$store.state[lang].currency;
    },
  },
};
</script>
