<template>
  <section @click="toggleProperty($event)">
    <i
      ref="check"
      :class="{
        'fal fa-stop': !property.fixed,
        'fas fa-check-square': property.fixed,
      }"
    />
    <input
      v-if="property.type == 'numeric'"
      type="number"
      v-model="num"
      @keyup="keyUpHandler"
      @keydown="keyDownHandler"
    />
    {{
      property.fixed
        ? property.name
        : property.name + ` (${property.price} ${currency})`
    }}
  </section>
</template>

<script>
export default {
  name: "ProjectProperty",
  props: ["property"],
  data() {
    return {
      num: 1,
    };
  },
  methods: {
    toggleProperty(e) {
      if (!this.property.fixed && e.target.nodeName != "INPUT") {
        const element = this.$refs["check"];
        if (element) {
          if (element.className.indexOf("fas fa-check-square") > -1) {
            element.className = element.className.replace(
              "fas fa-check-square",
              "fal fa-stop"
            );
          } else {
            element.className = element.className.replace(
              "fal fa-stop",
              "fas fa-check-square"
            );
          }
        }
      }
    },
    keyUpHandler() {
      if (this.num < 0) this.num = 1;
    },
    keyDownHandler(e) {
      if ("0123456789".indexOf(e.key) == -1 && e.key != "Backspace") {
        e.preventDefault();
      }
    },
    getPrice() {
      return this.property.price * this.num
        ? this.property.price * this.num
        : 0;
    },
    getNum() {
      return this.num;
    },
  },
  computed: {
    currency() {
      const { lang } = this.$store.state;
      return this.$store.state[lang].currency;
    },
  },
};
</script>
